import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Col, CustomInput, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType } from 'summer';
import { InformacaoSuspensao } from '../../../../models/dtos/mfp/informacaoSuspensao';
import { INDICATIVO_SUSPENSAO_EXIGIBILIDADE_ENUM_VALUES } from '../../../../models/enumerated/mfp/indicativoSuspensaoExigibilidadeEnum';

const ProcessoJudicialSuspensaoEditView: FC<DetailEditViewFormProps<InformacaoSuspensao>> = props => {
  const formProps = useForm<InformacaoSuspensao>({ defaultValues: props.selectedItem || {}, mode: 'onBlur' });

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps}>
      <Row>
        <Col>
          <InputGroupInline
            id="indicativoSuspensaoExigibilidade"
            label="Suspensão Exigibilidade"
            formProps={{ ...formProps, validation: { required: true } }}
            isPrependLabel={true}
            inputDropDownProps={{ optionsProvider: INDICATIVO_SUSPENSAO_EXIGIBILIDADE_ENUM_VALUES }}
            type={InputType.DROP_DOWN}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="dtDecisao"
            label="Data Decisão"
            formProps={{ ...formProps, validation: { required: true } }}
            isPrependLabel={true}
            type={InputType.DATE}
          />
        </Col>
      </Row>
      <Row>
        <Col style={{ paddingBottom: '20px' }}>
          <CustomInput
            type="switch"
            label="Depósito do Montante Integral"
            name="indicativoDeposito"
            id="checkDepositoMontanteIntegral"
            innerRef={formProps.register}
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default ProcessoJudicialSuspensaoEditView;
