import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import {
  boolColumn,
  DefaultSortProps,
  DetailManagerForm,
  DetailManagerFormProps,
  EditForm,
  EditViewProps,
  enumColumn,
  getSelectedItem,
  InputGroupInline,
  InputType,
  onlyNumber,
  OrderType,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, ProcessoJudicial } from '../../../../models/dtos/mfp/processoJudicial';
import { INDICATIVO_ACAO_JUDICIAL_ENUM_VALUES } from '../../../../models/enumerated/mfp/indicativoAcaoJudicialEnum';
import { INDICATIVO_MATERIA_PROCESSO_ENUM_VALUES } from '../../../../models/enumerated/mfp/indicativoMateriaProcessoEnum';
import { INDICATIVO_SUSPENSAO_EXIGIBILIDADE_ENUM_VALUES } from '../../../../models/enumerated/mfp/indicativoSuspensaoExigibilidadeEnum';
import { TIPO_PROCESSO_JUDICIAL_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoProcessoJudicialEnum';
import InfoComplementarProcessoJudicialIS from '../../../components/mfp/infoComplementarProcessoJudicialIS/InfoComplementarProcessoJudicialIS';
import ProcessoJudicialSuspensaoEditView from './ProcessoJudicialSuspensaoEditView';

interface ProcessoJudicialEditViewProps extends EditViewProps<ProcessoJudicial> {}

const ProcessoJudicialEditView: FC<ProcessoJudicialEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<ProcessoJudicial>({ defaultValues: { ...selectedItem }, mode: 'onBlur' });

  const suspensaoDefaultSortProps: DefaultSortProps[] = [
    {
      dataField: 'dtDecisao',
      order: OrderType.ASC,
    },
  ];

  const suspensaoFormProps: DetailManagerFormProps = {
    columns: [
      enumColumn(
        {
          dataField: 'indicativoSuspensaoExigibilidade',
          sort: true,
          text: 'Suspensão Exigibilidade',
        },
        INDICATIVO_SUSPENSAO_EXIGIBILIDADE_ENUM_VALUES
      ),
      {
        dataField: 'dtDecisao',
        sort: true,
        text: 'Data Decisão',
      },
      boolColumn({
        dataField: 'indicativoDeposito',
        sort: true,
        text: 'Depósito Montante Integral',
      }),
    ],
    detailView: ProcessoJudicialSuspensaoEditView,
    formProps,
    propName: 'informacoesSuspensao',
    title: 'Informações de suspensão',
  };

  const validateExtras = (processoJudicial: ProcessoJudicial): string[] => {
    const erros = [];

    if (processoJudicial.numeroProcesso.length > 0 && processoJudicial.numeroProcesso.length < 3) {
      erros.push('Número do Processo - Campo deve ser maior do que 3 caracteres.');
    }

    return erros;
  };

  return (
    <WindowComponent title="Manutenção de Processos Administrativos/Judiciais">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} validateExtras={validateExtras}>
        <Row>
          <Col>
            <InputGroupInline
              id="tipoProcesso"
              label="Tipo Processo"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe o tipo de processo"
              inputDropDownProps={{ optionsProvider: TIPO_PROCESSO_JUDICIAL_ENUM_VALUES }}
              type={InputType.DROP_DOWN}
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, ...onlyNumber(0, 20) } }}
              id="numeroProcesso"
              isPrependLabel={true}
              label="Número do Processo"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="indicativoAcaoJudicial"
              label="Autoria Ação Judicial"
              formProps={formProps}
              isPrependLabel={true}
              placeholder="Informe a autoria da ação judicial"
              inputDropDownProps={{ optionsProvider: INDICATIVO_ACAO_JUDICIAL_ENUM_VALUES }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="indicativoMateriaProcesso"
              label="Matéria do Processo/Alvará Judicial"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe a Matéria do Processo/Alvará Judicial"
              inputDropDownProps={{ optionsProvider: INDICATIVO_MATERIA_PROCESSO_ENUM_VALUES }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InfoComplementarProcessoJudicialIS
              id="infoComplementarProcessoJudicial"
              formProps={formProps}
              isPrependLabel={true}
              label="Informação Complementar Processo Judicial"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <DetailManagerForm
              {...props}
              defaultSortDirection={OrderType.ASC}
              defaultSorted={suspensaoDefaultSortProps}
              {...suspensaoFormProps}
              keyField="rowIndex"
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default ProcessoJudicialEditView;
