import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mfp/infoComplementarProcessoJudicial';

const InfoComplementarProcessoJudicialIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      type={InputType.INPUT_SEARCH}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'ufVara.sigla',
            text: 'UF',
          },
          {
            dataField: 'municipip.nome',
            text: 'Município',
          },
          {
            dataField: 'numeroIdentificacaoVara',
            text: 'Nº Identificação Vara',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'InfoComplementarProcessoJudicialSearchView',
      }}
    />
  );
};

export default InfoComplementarProcessoJudicialIS;
